div#Tecnologias {
  grid-template-columns: 1fr 1.2fr;

  div.content-text {
    p.content {
      text-align: center;
    }

    div.tecs {
      display: flex;
      flex-flow: row wrap;
      align-items: center;
      justify-content: center;

      div.tec {
        margin: 10px;
        width: 150px;

        display: flex;
        align-items: center;
        flex-direction: column;

        p.icon {
          font-size: 5rem;
        }
      }
    }
  }
}
