div.Page {
  flex-grow: 1;
  display: grid;
  grid-template-columns: 1fr 2fr;

  h1 {
    color: #f0636d;
    font-size: 2.5rem;
    text-align: center;

    margin-bottom: 30px;
  }

  p {
    line-height: 25px;
  }

  div.image {
    align-self: center;

    img {
      width: 100%;
    }
  }

  div.content-text {
    align-self: center;

    padding: 10px;
  }
}
