div#Main {
  div.content-text {
    div.skills {
      display: grid;

      div.skill {
        display: flex;
        align-items: center;
        justify-content: center;

        width: fit-content;

        margin: 10px;

        p.icon {
          color: #f0636d;
          font-size: 1.3rem;
        }

        p.text {
          margin: 0 10px;
          font-size: 1.1rem;
          text-align: start;
        }
      }
    }
  }
}
